<template>
  <v-container grid-list-xs>
    <v-overlay :value="overlayUnclosed">
      <v-progress-circular
          color="primary"
          indeterminate
          size="50"
      ></v-progress-circular>
    </v-overlay>
    <v-dialog
      transition="dialog-top-transition"
      max-width="600"
      v-model="confirmDeleteDialog"
    >
      <v-card>
        <v-toolbar
          color="primary"
          dark
        >About to delete a practitioner</v-toolbar>
        <v-card-text>
          Are you sure that you want to delete {{ staffName }}? This will delete everything associated with this record
        </v-card-text>
        <v-card-actions class="justify-end">
          <v-btn @click="$router.go(-1)">Cancel</v-btn>
          <v-spacer></v-spacer>
          <v-btn @click="deletePractitioner" color="error">Yes, Proceed</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-navigation-drawer
      app
      class="primary darken-1 white--text"
      clipped
      permanent
      right
      style="z-index: 3;"
    >
      <v-list class="white--text">
        <v-list-item>
          <v-btn class="secondary" dark @click="$router.push('/resource/view/practitioner/' + queries.id)">
            <v-icon light left>mdi-eye</v-icon>
            <span>View Health Profession</span>
          </v-btn>
        </v-list-item>
      </v-list>
    </v-navigation-drawer>
    <v-alert
      border="bottom"
      outlined
      v-if="staffName"
    >
      <b>Deleting: {{ staffName }}</b>
    </v-alert>
  </v-container>
</template>
<script>
export default {
  props: ["queries"],
  data() {
    return {
      valid: true,
      overlayUnclosed: false,
      confirmDeleteDialog: false,
      staffName: ""
    }
  },
  methods: {
    deletePractitioner() {
      fetch("/site/practitioner/deletePractitioner/" + this.queries.id, {
      method: "DELETE"
      }).then(response => response.json().then((response) => {
        if(response.message) {
          this.overlay
          this.$store.commit("setMessage", {
            type: response.type,
            text: response.message,
          });
          return this.$router.push("/resource/search/practitioner")
        }
      }).catch(() => {
        this.$store.commit("setMessage", {
          type: "error",
          text: "An error has occured, try later",
        });
        return this.$router.go(-1)
      })).catch(() => {
        this.$store.commit("setMessage", {
          type: "error",
          text: "An error has occured, try later",
        });
        return this.$router.go(-1)
      })
    }
  },
  created() {
    this.overlayUnclosed = true
    this.$fhirutils.resourceLookup("Practitioner/" + this.queries.id).then((name) => {
      this.overlayUnclosed = false
      this.staffName = name
      this.confirmDeleteDialog = true
    })
  }
}
</script>